.floatingdiv{
    justify-content: space-around;
    display: flex;
    background-color: white;
    box-shadow: var(--boxShadow);
    border-radius: 17px;
    align-items: center;
    height: 4.5rem;
}

.floatingdiv>img{
    transform: scale(0.5);

}

.floatingdiv>span{
    font-family: sans-serif;
    font-size:16px ;
}
