.projects{
   padding: 0.3rem 0.3rem;
   display: flex;
   height: 100vh; 
   margin-top: 4rem;
}

.w-right{
   position: relative;
}
.w-mainCircle{
   position: relative;
   left: 10rem;
   width: 20rem;
   height: 20rem;
   border-radius: 100%;
   box-shadow: var(--smboxShadow);
   top: 2rem;
   background: white;
}
.w-mainCircle>*{
   position: absolute;
}
.w-mainCircle>:nth-child(1){
   top: -3rem;
   left:7rem;

}
.w-mainCircle>:nth-child(2){
   left: -3rem;
   top:6rem;

}
.w-mainCircle>:nth-child(3){
   left: 7rem;
   top:6rem;

}
.w-mainCircle>:nth-child(4){
   left: 17rem;
   top:6rem;

}
.w-mainCircle>:nth-child(5){
   left: 7rem;
   top:16rem;

}

.w-secCircles{
   width: 7rem;
   height: 7rem;
   position: absolute;
   border-radius: 100%;
   border: 5px solid #ebebeb;
   display: flex;
   align-items: center;
   justify-content: center;
   background: none;
   box-shadow: var(--smboxShadow);
   background: white;
}
.w-secCircles>img{
   transform: scale(0.6);
}
.w-backcircle{
   position: absolute;
   height: 16rem;
   width: 16rem;
   border-radius: 100%;
   z-index: -1;
}

.bluecircle{
   background: #1969b8;
   left: 20rem;
   top: -1rem;
}
.yellowcircle{
   background:  #f10f6d;
   left: 18rem;
   top: 11rem;
}