.services{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 120vh;
    margin-bottom: 8rem;
    margin-top: 9rem;
    margin-bottom: 8rem;

}
.awesome{
    display: flex;
    flex-direction: column;
    position: relative;

}
.awesome>:nth-child(1){
    color: var(--black);
    font-size: 2.5rem;
    font-weight: bold;
}
.awesome>:nth-child(2){
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
}
.awesome>:nth-child(3){
    color: var(--gray);
    font-size: 14px;
    margin-top: 1rem;
}
.s-button{
    width: 8rem;
    margin-top: 1rem;
    height: 2rem;
}

.cards{
    position: relative;
}
.cards>*{
    position: absolute;
}
/*-----------------------*/

.s-blur2{
    left:14rem;
    top:8rem;
}

.s-blur1{
    left:-18rem;
    top:13rem;
}

