.projectcard{
    width: 20rem;
    height: 25rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 2px;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.26);
    border:7px solid var(--orangeCard) ;
    box-shadow: var(--boxShadow);
    border-radius: 20px;
 
    /* padding: 0px 20px 2rem 26px; */
}

.project-card>:nth-child(2){
    color: var(--black);
    font-size: 1.5rem;
    font-weight: bold;
    margin-left: 34%;
}
.project-card>:nth-child(4){
    color: var(--gray);
    font-size: 15px;
  
   text-align: center;
}
.project-tech-stack{
    display: flex;
    justify-content: space-evenly;
    
}
