.experience{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5rem;
    height: 30vh;
    margin-bottom: 8rem;

}
.achievement{
    display: flex;
    flex-direction: column;
   

}

.circle{
    width: 6rem;
    height: 6rem;
    background: white;
    border-radius: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 2rem;
}

.circle::before{
    border-radius: 100%;
    content: "";
    background-image:linear-gradient(to bottom,#87e6fb 0%, #df55bc 100% ) ;
    top: -10px;
    left: -10px;
    bottom: -10px;
    right: -10px;
position: absolute;
z-index:-1 ;
box-shadow: var(--boxShadow);
}

.achievement span:nth-of-type(2){
    color: var(--orange);
    font-weight: bold;
    margin: auto;
   
}
.achievement span:nth-of-type(1){
  margin-left: 40px;
    font-weight: bold;
}