.card{
    width: 18rem;
    height: 22rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 2px;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.26);
    border:7px solid var(--orangeCard) ;
    box-shadow: var(--boxShadow);
    border-radius: 20px;
    
    /* padding: 0px 20px 2rem 26px; */
}

.card span:nth-of-type(2){
    color: var(--gray);
    font-size: 16px;
    font-weight: bold;
   
}
.card>img{
    transform: scale(0.6);
    margin-bottom: -3rem;
}

img{
    transform: scale(0.6);
}