:root
{
  --yellow: #f5bf2c;
    --orange : #f10f6d;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard:rgba(223, 23, 133, 0.45);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}
*{
  scroll-behavior: smooth;
}
.App{
    padding: 0.5rem 3.5rem;
    overflow: hidden;
    color: var(--black);    
}

.button{
  border-radius: 35px;
  color: white;
  border: none;
  font-size:15px ;
  padding: 10px 25px 10px 25px ;
  background-color: #f10f6d;
  box-shadow: 0px 20px 24px 3px  rgba(235, 161, 17, 0,42) ;
}

.button:hover{
  background: white;
  color:var(--orange);
  cursor: pointer;
  border: 1px solid var(--orange);
}

  