.n-wrapper{
    height:10vh;
    display: flex;
    justify-content:space-between;
    /* box-shadow: 0px 20px 20px 2px  rgba(47, 55, 142, 0.45) */
    /* background-color: rgba(230, 188, 20, 0.45); */
}

.n-left{
    flex:1;
    align-items: center;
    display: flex;
    gap: 2rem;
}

.n-name{
    font-size:1.5rem;
    font-weight: bold;
    font-style: oblique;
}   
.halfName{
    font-size:1.5rem;
    font-weight: bold;
    font-style: oblique;
    color:  #f10f6d;
}
.n-right{
    flex:1;
    align-items: center;
    display: flex;
    font-weight:400 ;

}
.n-list{
    flex: 10;
}

.n-list>ul{
display: flex;
gap: 2rem;
margin-right: 4rem;
list-style-type: none;
}

.n-list>ul>li:hover{
    cursor: pointer;
    /* color: var(--orange); */

}

.n-button{
    flex: 2;
    margin-right: 30px;
}
